@use "styles/stylesFunctions.scss" as stylesFunctions;

.tableBody {
  @include stylesFunctions.paddingY(75px);
  padding-bottom: 0;
  row-gap: 80px;
  h2 {
    @include stylesFunctions.fontStyles(45px, 40px, 600, #000000, #000);
  }
  .tableContainer {
    background: #ffffff 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #00000029;
    border-radius: 10px;
    opacity: 1;
    padding: 55px 65px;
    .tableContainerHeader {
      margin-bottom: 40px;
      @include stylesFunctions.flexLayout(flex-start, center, row, 10px);
      svg {
        transform: scale(1.3);
      }
      h3 {
        margin: 0;
        @include stylesFunctions.fontStyles(24px, 18px, 600, #001736, #000);
      }
    }
    .moreInfo {
      margin-top: 40px;
      row-gap: 25px;
      label {
        padding: 16px 18px;
        @include stylesFunctions.flexLayout(flex-start, center, row, 16px);
        background: #ffffff 0% 0% no-repeat padding-box;
        border: 0.5px solid #00000041;
        border-radius: 5px;
        opacity: 1;
        input {
          @include stylesFunctions.fontStyles(24px, 18px, 500, #001736, #000);
        }
      }
      p {
        @include stylesFunctions.fontStyles(42px, 32px, 500, #001736, #000);
        margin: 0;
        span {
          opacity: 0.5;
        }
      }
      button {
        padding: 9px 30px;
        background: #ffaa03 0% 0% no-repeat padding-box;
        border-radius: 10px;
        opacity: 1;
        @include stylesFunctions.fontStyles(40px, 24px, 300, #ffffff, #000);
      }
    }
  }
}
.deleteBtn {
  background-color: transparent;
}

.formContainer {
  margin: 50px;
  background-color: white;
  box-shadow: 0px 3px 6px #00000029;
  border-radius: 10px;
  @include stylesFunctions.paddingY(80px);
}

.form {
  opacity: 1;
  @include stylesFunctions.flexLayout(center, flex-start, column, 50px);
  @include stylesFunctions.lg() {
    padding-inline: 17%;
  }

  .selectInput {
    width: 100%;
    padding: 13px 20px;
    @include stylesFunctions.flexLayout(center, flex-start, column, 17px);
    background: #ffffff 0% 0% no-repeat padding-box;
    border: 0.5px solid rgba(0, 0, 0, 0.1607843137);
    border-radius: 5px;
    opacity: 1;
    label {
      @include stylesFunctions.fontStyles(28px, 21px, 300, #000000, #000);
      opacity: 0.5;
    }
    select {
      width: 100%;
      @include stylesFunctions.fontStyles(39px, 29px, 500, #000000, #000);
    }
  }

  .addField {
    width: 80%;
    align-self: center;
    justify-self: center;
    border: 0.5px solid #00000029;
    border-radius: 5px;
    opacity: 1;
    cursor: pointer;
    @include stylesFunctions.flexLayout(center, center, row, 50px);
    @include stylesFunctions.fontStyles(28px, 21px, 500, #ffaa03, #000);
    @include stylesFunctions.paddingY(15px);
    @include stylesFunctions.lg() {
      width: 50%;
    }
  }

  .submiteBtn {
    width: 100%;
    button {
      width: 100%;
      background: #001736 0% 0% no-repeat padding-box;
      border-radius: 5px;
      opacity: 1;
      @include stylesFunctions.paddingY(20px);
      @include stylesFunctions.fontStyles(20px, 18px, 600, #000000, #000);
      color: white;
    }
  }

  .checkBox {
    label {
      @include stylesFunctions.flexLayout(flex-start, center, row, 10px);
      span {
        @include stylesFunctions.fontStyles(28px, 21px, 500, #000000, #000);
        a {
          text-decoration: underline;
        }
      }
    }
  }
  .addFieldWithLabel {
    width: 100%;
    @include stylesFunctions.flexLayout(space-between, center, row, initial);
    p {
      margin: 0;
      @include stylesFunctions.fontStyles(54px, 40px, 300, #000000, #000);
    }
    input[type="button"] {
      background: #ffffff 0% 0% no-repeat padding-box;
      border: 0.5px solid #000000;
      border-radius: 5px;
      opacity: 1;
      padding: 25px 15px;
      @include stylesFunctions.fontStyles(0px, 50px, 300, #000000, #000);
    }
  }
}
.customerInfoBody {
  row-gap: 40px;
  justify-content: center;
  .customerInfoContainer {
    width: 100%;
    @include stylesFunctions.flexLayout(center, center, column, 40px);
    .submiteBtn {
      width: 100%;
      @include stylesFunctions.lg() {
        width: 50%;
      }
      button {
        @include stylesFunctions.flexLayout(center, center, row, 10px);
        width: 100%;
        background: #001736 0% 0% no-repeat padding-box;
        border-radius: 5px;
        opacity: 1;
        @include stylesFunctions.paddingY(20px);
        @include stylesFunctions.fontStyles(20px, 18px, 600, #000000, #000);
        color: white;
        box-shadow: 0px 3px 6px #00000029;
        border-radius: 10px;
        svg {
          fill: currentColor;
        }
      }
    }
    .formInput {
      background: #ffffff 0% 0% no-repeat padding-box;
      border: 0.5px solid #0017363e;
      border-radius: 5px;
      opacity: 1;
      width: 100%;
      padding: 13px 25px;
      @include stylesFunctions.flexLayout(center, flex-start, column, 10px);
      @include stylesFunctions.lg() {
        width: 50%;
      }
      label {
      }
      input {
        @include stylesFunctions.fontStyles(29px, 22px, 500, #000000, #000);
      }
    }
  }
}

.colInput {
  @include stylesFunctions.flexLayout(center, center, column, 10px);

  input {
    max-width: 100%;
    @include stylesFunctions.fontStyles(29px, 22px, 500, #000000, #000);
  }
}
